@import "fuentes";
@import "variables";

body{
	font-family: 'Roboto', sans-serif;
	font-size: 400;
}

.font-weight-bold{
	font-weight: $font-size-bold;
}

.font-weight-light{
	font-weight: $font-size-light;
}

.font-weight-medium{
	font-weight: $font-size-medium;
}

.comienzo{
	margin-top: 80px !important;
}

.bg-color{
	background-color: $bg-color-header;
}

.navbar-brand{

	img{
		height: 50px;
		width: 50px;

		&:hover{
			transform: scale(1.7)
		}
	}
}

.navbar-nav{
	.nav-item{
		a{
			color: $color-navbar-letras;
			font-family: 'Roboto', sans-serif;
			font-size: 1.2rem;

			&:hover{
				color: $color-navbar-letras-hover !important;
			}
		}
	}
}





